import axios from 'axios'
import router from '../router'

/*
校验登录状态
未登录，跳转登录界面
*/
function CheckLoginState() {
  if(!localStorage.getItem('PovertyToken')) {
    router.push("/");
    return false;
  }
  return true;
}

/*
调用Post接口
url：接口地址
formData：请求参数
*/
function ApiPost(url, formData) {
  formData.token = localStorage.getItem('PovertyToken');
  return axios.post(url, formData)
}

/*
调用Get接口
url：接口地址
formData：请求参数
*/
function ApiGet(url) {
  return axios.get(url)
}

//登录接口
export const ApiUserLogin = (formData = {}) => {
  if(CheckLoginState()) {
    console.log(formData);
    return ApiGet("/Api/inst/select/searchResult")
  }
};

//登录接口
export const ApiUserLogin2 = (formData = {}) => {
  if(CheckLoginState()) {
    return ApiPost("/Api/inst/select/searchResult", formData)
  }
};
